body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
  }
  
  ol, ul {
    padding-left: 30px;
  }
  
  .board-row:after {
    clear: both;
    content: "";
    display: table;
  }
  
  .status {
    margin-bottom: 10px;
  }
  
  .square {
    background: #fff;
    border: 1px solid #999;
    float: left;
    font-size: 24px;
    font-weight: bold;
    line-height: 34px;
    height: 34px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 34px;
  }
  
  .square:focus {
    outline: none;
  }
  
  .kbd-navigation .square:focus {
    background: #ddd;
  }
  
  .game {
    display: flex;
    flex-direction: row;
  }
  
  .game-info {
    margin-left: 20px;
  }
  

  .custom-file-input::-webkit-file-upload-button {
    visibility: hidden;
  }
  .custom-file-input::before {
    content: 'Choose file';
    display: inline-block;
   
    border: 1px solid #999;
    border-radius: 3px;
    padding: 5px 8px;
    outline: none;
    white-space: nowrap;
    
  }
  .custom-file-input:hover::before {
    border-color: black;
  }
  .custom-file-input:active::before {
    background: -webkit-linear-gradient(top, #e3e3e3, #f9f9f9);
  }

  .hidden {
    display: none;
  }

.cancel-btn { 
    margin-top: 1em; 
  }
  .cancel-btn:before {
    content: "âœ–";
    color: gray;
    padding:0.5em;
    cursor: pointer;
  }

  .color-btn {
    cursor: pointer;    
    height: 1rem;
    max-width: 2rem;
    width: 1rem;
    flex: 1;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.3);
    background-size: cover;
    background-position: center;
    margin-right: 5px;
    border: 1px solid #353535;
    border-radius: 50%;
    display: inline-block;
  }

  .draw_controls {
    display: flex;
    margin-top: 10px;
    margin-bottom: 10px;
  }

  .block-picker {
    position: absolute !important;
    z-index: 999 !important;
  }

  .color-btn-wrap {
    display: flex;
  }

  #brush_size {
    cursor: pointer;
  }

  div.dropdown {
    align-content: center;
  }

  .btn-cust, .dropdown-item-cust {
    color: #ddc;
    -moz-appearance: none ;
    appearance: none ;
    -webkit-appearance: none ;
    text-align: center ;
    text-transform: uppercase ;
    font-size: 13px ;
    font-weight: 600 ;
    line-height: 16px ;
    font-family: inherit ;
    border: 1px #ddc solid ;
    border-radius: 2px ;
    background-color: transparent ;
    cursor: pointer ;
    display: inline-block ;
    margin: 0 ;
    white-space: nowrap ;
    position: relative ;
    -webkit-user-select: none ;
    -moz-user-select: none ;
    -ms-user-select: none ;
    user-select: none ;
    box-sizing: border-box ;
    z-index: 1 ;
  }

  .controls_cust {
    margin-left: 5px;
    margin-left: 5px;
    margin-right: 5px;
    margin-top: 10px;
  }

  .user_title {
    display: flex;
    margin-top: 15px;
    margin-bottom: 10px;
  }

  .user_name {
    padding: 10px;
  }
  
  .sticker_item {
    
    width: 100%;
    display: flex;
  }

  .sticker_item img {
    margin: 0 auto;
    background-color: #ffff;
  }

  .draw_pad div {
    margin: 0 auto;
  }

  .brush.label{
    display: flex;
    
  }
  .brush.label label {
    margin: 0 auto;
  }
  .brush.div {
    display: block;
    padding: 10px;
  }

  .brush.input {
    display: block;
  }